import { Component, OnInit, Output, EventEmitter, OnChanges, Input } from '@angular/core';
import { CusViewHttpService } from '../../../../../http/custom-view/custom-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { AddPartyRelationshipTypeBean } from '../relationship-type-add/bean/addPartyRelationshipType.bean';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { checkNumber } from 'app/@uisftech/common/service/validator';
// import { Panduan } from '../../../../../constant/custView.codeValue';

@Component({
  selector: 'app-relationship-type-update',
  templateUrl: './relationship-type-update.component.html',
  styleUrls: ['./relationship-type-update.component.scss'],
  providers: [CusViewHttpService, Commfunc]
})
export class RelationshipTypeUpdateComponent implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  items: any[];
  public partyRelationshipTypeId: string;
  public parentTypeId: string;//父级关系类型编号
  public description: string;
  public createUserId: string;
  public createUserName: string;
  public relationshipType: string;
  public relationshipName: string;
  public title: string = '关系类型新增';
  public parentId: any = [];
  public userform: FormGroup; //校验
  public checkNumber = checkNumber;//校验数字
  public panduan: any[] = [];
  public addPartyRelationshipTypeBean: AddPartyRelationshipTypeBean = new AddPartyRelationshipTypeBean();
  code:any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public fb: FormBuilder,
  ) {
    this.codeVal();
    this.panduan=this.code['Panduan'];
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'parentTypeId': new FormControl('', Validators.required),
      'parentTypeName': new FormControl('', Validators.required),
      'relationshipType': new FormControl('', Validators.required),
      'relationshipName': new FormControl('', Validators.required),
      'description': new FormControl(''),
      'createUserId': new FormControl(''),
      'createUserName': new FormControl(''),
    });
    // let param = { queryType: '1', relationshipType: this.relationshipType,parentTypeId: '' }
    // let ob = this.httpService.selectPartyRelationshipType(param);
    // ob.subscribe(data => {
    //   this.items = data.partyRelationshipTypeList;
    //   this.parentId=[];
    //   this.parentId = this.commfunc.toChange(this.items,'relationshipName','relationshipType')
    //   this.parentId.unshift({label:'请选择',value:''});
    //   this.addPartyRelationshipTypeBean.parentTypeId='';
    // })

  }
  ngOnChanges() {
    if (this.inValue) {
      this.addPartyRelationshipTypeBean = new AddPartyRelationshipTypeBean();
      if(this.inValue.parent){
        this.addPartyRelationshipTypeBean.parentTypeId = this.inValue.parent.data.partyRelationshipTypeId;
        this.addPartyRelationshipTypeBean.parentTypeName = this.inValue.parent.data.partyRelationName;
      }
      this.addPartyRelationshipTypeBean.relationshipType = this.inValue.data.partyRelationshipTypeId;
      this.addPartyRelationshipTypeBean.relationshipName = this.inValue.data.partyRelationName;
      this.addPartyRelationshipTypeBean.createUserId = this.inValue.data.createUserId;
      this.addPartyRelationshipTypeBean.createUserName = this.inValue.data.createUserName;
      this.addPartyRelationshipTypeBean.description = this.inValue.data.description;
      // this.addPartyRelationshipTypeBean.createUserId=JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;            
      // this.addPartyRelationshipTypeBean.createUserName=JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;  
      // let param = { queryType: '1', relationshipType: this.relationshipType,parentTypeId: '' }
      // let ob = this.httpService.selectPartyRelationshipType(param);
      // ob.subscribe(data => {
      //   this.items = data.partyRelationshipTypeList;
      //   this.parentId=[];
      //   this.parentId = this.commfunc.toChange(this.items,'relationshipName','relationshipType')
      //   this.parentId.unshift({label:'请选择',value:''});
      //   this.addPartyRelationshipTypeBean.parentTypeId='';
      // })            
    }
  }
  //添加
  addSaveBtn() {
    //校验的
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    //处理是否提交
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    // this.addPartyRelationshipTypeBean.parentTypeId=this.parentTypeId;
    let ob = this.httpService.updatePartyRelationshipType(this.addPartyRelationshipTypeBean);
    ob.subscribe(data => {
      this.outValue.emit(false);
      this.addPartyRelationshipTypeBean = new AddPartyRelationshipTypeBean();
    })

  }
  keyupNum(event) {
    if (checkNumber(null, event.target.value)) {
      event.target.value = '';
    }
  }
  //取消
  addCloseBtn() {
    this.outValue.emit(false);
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }
}
