import { Component, OnInit } from '@angular/core';
import { CusViewHttpService } from '../../../../../http/custom-view/custom-view.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';
// import { DomPanduan } from '../../../../../constant/custView.codeValue';
import { tableMessage } from "app/@uisftech/common/constant/config";
@Component({
  selector: 'app-relationship-type-query',
  templateUrl: './relationship-type-query.component.html',
  styleUrls: ['./relationship-type-query.component.scss'],
  providers: [CusViewHttpService, Commfunc, ConfirmationService],
})
export class RelationshipTypeQueryComponent implements OnInit {
  tabMesg: any = tableMessage;//暂无数据
  title: string = '关系类型查询';
  public inValue: any;
  public items: any = [];
  //模态框的标题
  public headerTitle: string = "修改";
  //模态框是否显示
  public display: boolean = false;
  public displayUpdate: boolean = false;
  public showModel: string;
  //提示信息
  public msgs: Message[] = [];
  public domPanduan = [];
  //修改的参数
  public updateValue: any = [];
  public partyRelationshipTypeId: string = '01';//父级关系类型编号
  public relationshipType: string;//子级关系类型编号
  public parentId: any = [];
  public relationship: any = [];
  public selectType: string;
  // public queryType:string='1';
  code:any;
  constructor(
    public httpService: CusViewHttpService,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService
  ) {
    this.codeVal();
    this.domPanduan=this.code['DomPanduan'];
  }
  ngOnInit() {
    // ;

    // this.doSelet();
    this.query();
  }
  //新增按钮事件
  addClick(col) {
    // this.inValue = _.clone({ createUserId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId });
    this.inValue = _.clone(col);
    this.headerTitle = '关系类型新增';
    this.display = true;
  }
  //添加的回调
  addCall(param) {
    this.display = param;
    this.query();
  }
  //修改按钮事件
  updateClick(col) {
    this.inValue = _.clone(col);
    this.headerTitle = '关系类型修改';
    this.displayUpdate = true;
  }
  //修改的回调
  updateCall(param) {
    this.displayUpdate = param;
    this.query();
  }
  //查询接口
  query() {
    // // ;
    // let param = { partyRelationshipTypeId: this.partyRelationshipTypeId, relationshipType: this.relationshipType }
    // let ob = this.httpService.selectPartyRelationshipType(param);
    // ob.subscribe(data => {
    //   this.items = data.partyRelationshipTypeList;
    // })
    let param = {  relationshipType:'' }
    let ob = this.httpService.selectPartyRelationshipTypeTreeView(param);
    ob.subscribe(data => {
      // this.items = data.partyRelationshipTypeList;
      if (data.returnCode.code == 'success') {
        this.items = JSON.parse(data.prtTree.replace(/'/g,'"'));
        console.log(this.items);
      }
    })
  }
  // doSelet() {
  //   let param = { partyRelationshipTypeId: this.partyRelationshipTypeId, relationshipType: this.relationshipType, queryType: '1' }
  //   let ob = this.httpService.selectPartyRelationshipType(param);
  //   ob.subscribe(data => {
  //     // this.items = data.partyRelationshipTypeList;
  //     if (data.returnCode.code == 'success') {
  //       this.relationshipType = '';
  //       this.relationship = [];
  //       this.relationship = this.commfunc.toChange(data.partyRelationshipTypeList, 'relationshipName', 'relationshipType');
  //       this.relationship.unshift({ label: '请选择', value: '' });
  //     }

  //   })

  // }

  //删除按钮事件
  deleteClick(col) {
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        //条件
        // ;
        let param = { parentTypeId: col.parent.data.partyRelationshipTypeId, relationshipType: col.data.partyRelationshipTypeId };
        this.httpService.deletePartyRelationshipType(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            // this.selectFreeGroupBean.pageNum = 1;
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            this.query();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }

  //按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
  }
  //码值
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
  }

}
