import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { RelationshipTypeQueryComponent } from "./relationship-type-maintenance/relationship-type-query/relationship-type-query.component";
import { RelationshipTypeAddComponent } from "./relationship-type-maintenance/relationship-type-add/relationship-type-add.component";
import { RelationshipTypeUpdateComponent } from "./relationship-type-maintenance/relationship-type-update/relationship-type-update.component";
import { TopologicalRelationshipMaintenanceComponent } from "./topological-relationship-maintenance/topological-relationship-maintenance.component";

const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'relationship-type-query', component: RelationshipTypeQueryComponent},
            {path: 'relationship-type-add', component: RelationshipTypeAddComponent},
            {path: 'relationship-type-update', component: RelationshipTypeUpdateComponent},
            {path: 'topological-relationship-maintenance', component: TopologicalRelationshipMaintenanceComponent},
            {path: '', redirectTo: 'relationship-type-query', pathMatch: 'full'}
        ]
    }
];
export const CustomRelationshipMaintenanceRouting: ModuleWithProviders = RouterModule.forChild(routes);